ser-autocomplete {
  position: relative;
  display: block; }

ser-autocomplete .focus input,
ser-autocomplete .focus .addon {
  border-bottom-left-radius: 0 !important;
  border-bottom-right-radius: 0 !important; }

ser-autocomplete .results {
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-box-direction: normal;
  position: absolute;
  background: white;
  z-index: 11;
  width: 100%;
  max-height: 200px;
  overflow: auto;
  border: 1px solid #bfbfbf;
  border-top-width: 0;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px; }

ser-autocomplete .results .item {
  padding: 0 4px; }

ser-autocomplete .not-results {
  padding: 8px; }

ser-autocomplete .results .item:hover {
  background: #efefef; }
