.ser-date-wrapper {
  width: 100%; }

.ser-date-wrapper .clear {
  font-size: 20px;
  font-weight: inherit; }

.date-picker {
  width: 170px;
  height: 25px;
  padding: 0;
  border: 0;
  line-height: 25px;
  padding-left: 10px;
  font-size: 12px;
  font-family: 'Open Sans', "Century Gothic",CenturyGothic,AppleGothic,sans-serif;
  font-weight: bold;
  cursor: pointer;
  color: #303030;
  position: relative;
  z-index: 2; }

.date-picker-wrapper {
  position: absolute;
  z-index: 1;
  border: 1px solid #bfbfbf;
  background-color: #efefef;
  font-size: 12px;
  line-height: 20px;
  color: #aaa;
  font-family: 'Open Sans', "Century Gothic",CenturyGothic,AppleGothic,sans-serif;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.5);
  box-sizing: initial; }

.date-picker-wrapper.inline-wrapper {
  position: relative;
  box-shadow: none;
  display: inline-block; }

.date-picker-wrapper.single-date {
  width: auto; }

.date-picker-wrapper .footer {
  font-size: 11px;
  padding-top: 3px; }

.date-picker-wrapper b {
  color: #666;
  font-weight: 700; }

.date-picker-wrapper a {
  color: #6bb4d6;
  text-decoration: underline; }

.date-picker-wrapper .month-name {
  text-transform: uppercase; }

.date-picker-wrapper .month-wrapper {
  border: 1px solid #bfbfbf;
  border-radius: 3px;
  background-color: #fff;
  padding: 5px;
  cursor: default;
  position: relative;
  _overflow: hidden; }

.date-picker-wrapper .month-wrapper table {
  width: 190px;
  float: left; }

.date-picker-wrapper .month-wrapper table.month2 {
  width: 190px;
  float: left; }

.date-picker-wrapper .month-wrapper table th,
.date-picker-wrapper .month-wrapper table td {
  vertical-align: middle;
  text-align: center;
  line-height: 14px;
  margin: 0px;
  padding: 0px; }

.date-picker-wrapper .month-wrapper table .day {
  padding: 8px 0;
  width: 32px;
  line-height: 1;
  font-size: 12px;
  margin-bottom: 1px;
  color: #ccc;
  cursor: default; }

.date-picker-wrapper .month-wrapper table div.day.lastMonth,
.date-picker-wrapper .month-wrapper table div.day.nextMonth {
  color: #999;
  cursor: default; }

.date-picker-wrapper .month-wrapper table .day.checked {
  background-color: #9cdbf7; }

.date-picker-wrapper .month-wrapper table .week-name {
  height: 20px;
  line-height: 20px;
  font-weight: 100;
  text-transform: uppercase; }

.date-picker-wrapper .month-wrapper table .day.has-tooltip {
  cursor: help !important; }

.date-picker-wrapper .time label {
  white-space: nowrap; }

.date-picker-wrapper .month-wrapper table .day.toMonth.valid {
  color: #333;
  cursor: pointer; }

.date-picker-wrapper .month-wrapper table .day.toMonth.hovering {
  background-color: #cdecfa; }

.date-picker-wrapper .month-wrapper table .day.nextMonth,
.date-picker-wrapper .month-wrapper table .day.lastMonth {
  display: none; }

.date-picker-wrapper .month-wrapper table .day.real-today {
  background-color: #ffe684; }

.date-picker-wrapper .month-wrapper table .day.real-today.checked,
.date-picker-wrapper .month-wrapper table .day.real-today.hovering {
  background-color: #70ccd5; }

.date-picker-wrapper table .caption {
  height: 40px; }

.date-picker-wrapper table .caption .next,
.date-picker-wrapper table .caption .prev {
  padding: 0 5px;
  cursor: pointer; }

.date-picker-wrapper table .caption .next:hover,
.date-picker-wrapper table .caption .prev:hover {
  background-color: rgba(255, 255, 255, 0.3);
  color: #fff; }

.date-picker-wrapper .gap {
  position: relative;
  z-index: 1;
  width: 15px;
  height: 100%;
  font-size: 0;
  line-height: 0;
  float: left;
  top: -5px;
  margin: 0 10px -10px;
  visibility: hidden;
  height: 100px; }

.date-picker-wrapper .gap .gap-lines {
  height: 100%;
  overflow: hidden; }

.date-picker-wrapper .gap .gap-line {
  height: 15px;
  width: 15px;
  position: relative; }

.date-picker-wrapper .gap .gap-line .gap-1 {
  z-index: 1;
  height: 0;
  border-left: 8px solid white;
  border-top: 8px solid #eee;
  border-bottom: 8px solid #eee; }

.date-picker-wrapper .gap .gap-line .gap-2 {
  position: absolute;
  right: 0;
  top: 0px;
  z-index: 2;
  height: 0;
  border-left: 8px solid transparent;
  border-top: 8px solid white; }

.date-picker-wrapper .gap .gap-line .gap-3 {
  position: absolute;
  right: 0;
  top: 8px;
  z-index: 2;
  height: 0;
  border-left: 8px solid transparent;
  border-bottom: 8px solid white; }

.date-picker-wrapper .gap .gap-top-mask {
  width: 6px;
  height: 1px;
  position: absolute;
  top: -1px;
  left: 1px;
  background-color: #eee;
  z-index: 3; }

.date-picker-wrapper .gap .gap-bottom-mask {
  width: 6px;
  height: 1px;
  position: absolute;
  bottom: -1px;
  left: 7px;
  background-color: #eee;
  z-index: 3; }

.date-picker-wrapper .selected-days {
  display: none; }

.date-picker-wrapper .drp_top-bar {
  line-height: 1.4;
  position: relative;
  padding: 10px 40px 10px 0; }

.date-picker-wrapper .drp_top-bar .error-top {
  display: none; }

.date-picker-wrapper .drp_top-bar .normal-top {
  display: none; }

.date-picker-wrapper .drp_top-bar .default-top {
  display: block; }

.date-picker-wrapper .drp_top-bar.error .default-top {
  display: none; }

.date-picker-wrapper .drp_top-bar.error .error-top {
  display: block;
  color: red; }

.date-picker-wrapper .drp_top-bar.normal .default-top {
  display: none; }

.date-picker-wrapper .drp_top-bar.normal .normal-top {
  display: block; }

.date-picker-wrapper .drp_top-bar .apply-btn {
  position: absolute;
  right: 0px;
  top: 6px;
  padding: 3px 5px;
  margin: 0;
  font-size: 12px;
  border-radius: 4px;
  cursor: pointer;
  color: #d9eef7;
  border: solid 1px #0076a3;
  background: #0095cd;
  background: -webkit-gradient(linear, left top, left bottom, from(#00adee), to(#0078a5));
  background: -moz-linear-gradient(top, #00adee, #0078a5);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00adee', endColorstr='#0078a5');
  color: white;
  line-height: initial; }

.date-picker-wrapper .drp_top-bar .apply-btn.disabled {
  cursor: pointer;
  color: #606060;
  border: solid 1px #b7b7b7;
  background: #fff;
  background: -webkit-gradient(linear, left top, left bottom, from(#fff), to(#ededed));
  background: -moz-linear-gradient(top, #fff, #ededed);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#ededed'); }

/*time styling*/
.date-picker-wrapper .time {
  position: relative; }

.date-picker-wrapper.single-month .time {
  display: block; }

.date-picker-wrapper .time input[type=range] {
  vertical-align: middle;
  width: 129px;
  padding: 0;
  margin: 0;
  height: 20px; }

.date-picker-wrapper .time1,
.time2 {
  width: 180px;
  padding: 0 5px;
  text-align: center; }

.date-picker-wrapper .time1 {
  float: left; }

.date-picker-wrapper .time2 {
  float: right; }

.date-picker-wrapper .hour,
.minute {
  text-align: right; }

.date-picker-wrapper .hide {
  display: none; }

.date-picker-wrapper .first-date-selected {
  border-bottom-left-radius: 5px;
  border-top-left-radius: 5px; }

.date-picker-wrapper .last-date-selected {
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px; }

.date-picker-wrapper .month-wrapper table .day.checked.first-date-selected,
.date-picker-wrapper .month-wrapper table .day.checked.last-date-selected {
  background-color: #49e;
  color: white; }

.date-picker-wrapper .date-range-length-tip {
  position: absolute;
  margin-top: -4px;
  margin-left: -8px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
  display: none;
  background-color: yellow;
  padding: 0 6px;
  border-radius: 2px;
  font-size: 12px;
  line-height: 16px;
  -webkit-filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.3));
  -moz-filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.3));
  -ms-filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.3));
  -o-filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.3));
  filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.3)); }

.date-picker-wrapper .date-range-length-tip:after {
  content: '';
  position: absolute;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 4px solid yellow;
  left: 50%;
  margin-left: -4px;
  bottom: -4px; }

.date-picker-wrapper.two-months.no-gap .month1 .next,
.date-picker-wrapper.two-months.no-gap .month2 .prev {
  display: none; }

.date-picker-wrapper .week-number {
  padding: 5px 0;
  line-height: 1;
  font-size: 12px;
  margin-bottom: 1px;
  color: #999;
  cursor: pointer; }

.date-picker-wrapper .week-number.week-number-selected {
  color: #4499EE;
  font-weight: bold; }

.date-picker-wrapper .month-wrapper table .day.real-today,
.date-picker-wrapper .month-wrapper table .day.real-today.checked,
.date-picker-wrapper .month-wrapper table .day.real-today.hovering,
.date-picker-wrapper.single-date .first-date-selected {
  border-radius: 5px; }
