sidebar {
  display: block;
  position: relative;
  width: 250px;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  background: white;
  z-index: 3;
  -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);
  -moz-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12); }

sidebar .logo {
  position: relative;
  height: 80px;
  padding: 0 6px;
  margin-bottom: 16px;
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-grid-row-align: center;
  align-items: center;
  -webkit-align-content: center;
  align-content: center;
  max-width: 100%; }

sidebar .logo::after {
  content: '';
  position: absolute;
  width: 170px;
  bottom: -2px;
  left: 50%;
  transform: translateX(-50%);
  height: 2px; }

sidebar .logo .name {
  font-size: 1.14286rem;
  padding-left: 16px;
  font-weight: 700;
  text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); }

.sidebar-collapse,
.sidebar-show {
  position: absolute;
  background: white;
  color: #2f323a;
  width: 33px;
  height: 37px;
  top: 8px;
  left: -2px;
  border-radius: 0 6px 6px 0;
  cursor: pointer;
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-grid-row-align: center;
  align-items: center;
  -webkit-align-content: center;
  align-content: center;
  max-width: 100%; }

.sidebar-show {
  display: none;
  width: 37px; }

@media (max-width: 900px) {
  .sidebar-show {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex; }
  .sidebar-collapse {
    display: none; } }

sidebar .navigation a {
  text-decoration: none;
  color: black; }

sidebar .navigation a:hover {
  color: #653182;
  text-decoration: underline; }

sidebar .navigation ul {
  list-style: none;
  padding: 0; }

sidebar .navigation > ul > li > .title {
  padding: 16px 8px;
  text-transform: uppercase;
  text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3);
  text-decoration: none;
  font-weight: 600;
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-grid-row-align: center;
  align-items: center;
  -webkit-align-content: center;
  align-content: center;
  max-width: 100%; }

sidebar .navigation ul.submenu {
  padding: 0 0 24px 24px; }

sidebar .navigation ul.submenu li {
  padding: 4px 0; }

sidebar .navigation ul.submenu li a.active {
  color: #653182; }

sidebar + .backdrop {
  display: none;
  z-index: 20; }

@media (max-width: 1000px) {
  sidebar {
    position: fixed;
    z-index: 30;
    left: -250px;
    -webkit-transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
    transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1); }
  sidebar.show {
    left: 0;
    -webkit-transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
    transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1); }
  sidebar.show + .backdrop {
    display: block; } }
