.preview-file {
  background: transparent;
  box-shadow: none;
  max-width: 90%;
  max-height: 90%; }

.preview-file-header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  color: white; }

.preview-file-header .title {
  padding: 10px; }

.preview-file .img-wrapper {
  overflow-y: auto; }

.preview-file .img-wrapper img {
  max-width: 100%; }

.preview-file object {
  max-width: 100%;
  max-height: 100%;
  width: 3000px;
  height: 3000px;
  border: 0; }

/*[zoom-image] img {
    vertical-align: top;
}*/
.spzoom-zoom {
  z-index: 97;
  border: 1px solid rgba(0, 0, 0, 0.2);
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  width: 350px;
  height: 350px; }

.spzoom-tracker {
  border-width: 1px;
  border-style: solid;
  background-color: #fff;
  border-color: #ccc;
  z-index: 97; }

.spzoom-title {
  font-family: Verdana, Helvetica, Arial, sans-serif;
  font-size: 10px;
  color: #fff;
  background-color: #222;
  padding: 3px 5px;
  line-height: 14px; }

.spzoom-loader {
  width: 24px;
  height: 24px;
  background: url("../images/spzoom-loader.gif") 50% 50% no-repeat scroll #fff;
  border: 1px solid #ccc; }
