.switch {
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-grid-row-align: center;
  align-items: center;
  -webkit-align-content: center;
  align-content: center;
  max-width: 100%;
  cursor: pointer;
  user-select: none; }

.switch input {
  display: none; }

.switch i {
  display: inline-block;
  cursor: pointer;
  padding-right: 20px;
  margin-right: 16px;
  transition: all ease 0.2s;
  -webkit-transition: all ease 0.2s;
  border-radius: 20px;
  box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.5); }

.switch i:before {
  display: block;
  content: '';
  width: 20px;
  height: 20px;
  margin: 2px;
  border-radius: 20px;
  background: white;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5); }

.switch input:not(:checked) + i {
  background-color: grey; }

.switch :checked + i {
  padding-right: 0;
  padding-left: 20px;
  -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.5), inset 0 0 40px #3ed400;
  -moz-box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.5), inset 0 0 40px #3ed400;
  box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.5), inset 0 0 40px #3ed400;
  background: #a9e296; }
