.crud-utils {
  margin: 16px;
  padding: 16px;
  background: white;
  -webkit-box-shadow: 4px 4px 20px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 4px 4px 20px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 4px 4px 20px 0px rgba(0, 0, 0, 0.2); }

.crud-utils .tools {
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-grid-row-align: center;
  align-items: center;
  -webkit-align-content: center;
  align-content: center;
  max-width: 100%; }

.crud-utils .tools .search {
  -webkit-box-flex: 0;
  -webkit-flex: 0 0 auto;
  flex: 0 0 auto;
  box-sizing: border-box;
  margin: 0 8px 0 0; }

.crud-utils .tools .search input {
  position: relative;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  margin: 0;
  width: calc(100% - 24px);
  padding: 6px 12px;
  font-family: inherit;
  font-size: 0.92857rem;
  color: rgba(0, 0, 0, 0.7);
  background-image: none;
  background-clip: padding-box;
  border: 1px solid #989898;
  border-radius: 6px;
  outline: none;
  -webkit-appearance: none; }

.crud-utils .filters {
  padding-top: 10px; }

.crud-wrapper {
  position: relative;
  margin: 16px;
  background: white;
  -webkit-box-shadow: 4px 4px 20px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 4px 4px 20px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 4px 4px 20px 0px rgba(0, 0, 0, 0.2); }

.crud-wrapper.loading {
  min-height: 300px; }

.crud-wrapper[md-virtual-repeat-container] {
  height: calc(100vh - 230px);
  overflow-x: auto; }

.crud-wrapper[md-virtual-repeat-container] .loader-wrapper {
  height: calc(100vh - 230px); }

.crud-wrapper .item-wrapper {
  position: relative;
  padding: 24px 16px;
  border-bottom: 1px solid #bfbfbf;
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap; }

.crud-wrapper .item-wrapper:last-child {
  border-bottom: none; }

.crud-wrapper .item-wrapper:hover {
  background-color: rgba(0, 0, 0, 0.06); }

.crud-wrapper .item-wrapper .title {
  font-size: 1.14286rem; }

.crud-wrapper .item-wrapper .options {
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-grid-row-align: center;
  align-items: center;
  -webkit-align-content: center;
  align-content: center;
  max-width: 100%; }

@media (max-width: 600px) {
  .crud-utils {
    margin: 16px 0; }
  .crud-utils button,
  .crud-utils .button {
    margin-top: 8px; }
  .crud-wrapper {
    margin: 16px 0; }
  .crud-utils .tools .search {
    -webkit-flex: 0 0 100%;
    flex: 0 0 100%;
    margin: 0; } }

.chip {
  padding: 4px 12px;
  background: #efefef;
  margin-left: 8px;
  border-radius: 6px; }

.chip.blue {
  background: #84d0fd; }

.chip.light-blue {
  background: #c9f5ff; }

.chip.green {
  background: #87ec6d; }

.chip.yellow {
  background: #fff242; }

.chip.orange {
  background: #fbbc48; }

.chip.red {
  background: #fd4e4e; }
