fieldset {
  -webkit-padding-before: 16px;
  padding: 16px 16px 8px;
  margin: 0;
  border-radius: 4px;
  font-family: inherit;
  border: 1px solid #dcdcdc;
  position: relative; }

legend {
  position: relative;
  top: 2px;
  font-size: 0.92857rem;
  text-transform: uppercase;
  padding: 0 16px;
  margin-left: -10px;
  user-select: none; }

label {
  user-select: none; }

.form-group {
  position: relative;
  padding: 0 4px 8px; }

.form-group.no-padding {
  padding-bottom: 0; }

.form-group > label:not(.switch) {
  display: inline-block;
  font-size: 0.71429rem;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 8px;
  letter-spacing: 1px; }

.form-group .input-group {
  position: relative;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.form-group input:not(.not-styled),
.form-group textarea:not(.not-styled),
.form-group select:not(.not-styled) {
  position: relative;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  margin: 0;
  width: calc(100% - 24px);
  padding: 6px 12px;
  font-family: inherit;
  font-size: 0.92857rem;
  color: rgba(0, 0, 0, 0.7);
  background-image: none;
  background-clip: padding-box;
  border: 1px solid #989898;
  border-radius: 6px;
  outline: none;
  -webkit-appearance: none; }

.form-group select:not(.not-styled),
.form-group .selector-container {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  box-sizing: border-box; }

.form-group input:not(.not-styled):focus,
.form-group textarea:not(.not-styled):focus,
.form-group .selector-container.open .selector-input,
.form-group select:not(.not-styled):focus {
  border-color: #66dee9;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 222, 233, 0.6); }

input[disabled],
textarea[disabled],
select[disabled] {
  cursor: not-allowed;
  user-select: none; }

.input-group .addon {
  padding: 3px 12px;
  margin-bottom: 0;
  font-weight: normal;
  color: #303030;
  text-align: center;
  border: 1px solid #989898;
  background-color: #f5f5f5;
  border-radius: 6px;
  user-select: none;
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-grid-row-align: center;
  align-items: center;
  -webkit-align-content: center;
  align-content: center;
  max-width: 100%; }

.input-group > input:not(.not-styled):first-child,
.input-group > textarea:not(.not-styled):first-child,
.input-group > select:not(.not-styled):first-child,
.input-group > .selector-container:first-child .selector-input,
.input-group > .addon:first-child {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0; }

.input-group .addon:first-child {
  border-right-width: 0; }

.input-group > input:not(.not-styled):last-child,
.input-group > textarea:not(.not-styled):last-child,
.input-group > select:not(.not-styled):last-child,
.input-group > .selector-container:last-child .selector-input,
.input-group > .addon:last-child {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0; }

.input-group .addon:last-child {
  border-left-width: 0; }

.input-group > input:not(:first-child):not(:last-child),
.input-group > textarea:not(:first-child):not(:last-child),
.input-group > select:not(:first-child):not(:last-child),
.input-group > .selector-container:not(:first-child):not(:last-child) .selector-input,
.input-group > .addon:not(:first-child):not(:last-child) {
  border-radius: 0; }

.input-group > input:first-child:last-child:only-child,
.input-group > textarea:first-child:last-child:only-child,
.input-group > select:first-child:last-child:only-child,
.input-group > .selector-container:first-child:last-child:only-child .selector-input,
.input-group > .addon:first-child:last-child:only-child {
  border-radius: 6px; }

.form-group [ng-messages].ng-active {
  margin-top: 8px;
  color: red;
  user-select: none; }

.form-group input.ng-invalid.ng-dirty:not(.not-styled),
.form-group textarea.ng-invalid.ng-dirty:not(.not-styled),
.form-group select.ng-invalid.ng-dirty:not(.not-styled),
.form-group .selector-container.ng-invalid.ng-dirty .selector-input {
  border-color: #fd4e4e;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(253, 78, 78, 0.6);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(253, 78, 78, 0.6); }

