.steps {
  list-style: none;
  margin: 0;
  padding: 16px 8px;
  display: table;
  table-layout: fixed;
  width: 100%;
  color: #849397; }

.steps > li {
  position: relative;
  display: table-cell;
  text-align: center;
  font-size: 0.92857rem; }

.steps > li:before {
  content: attr(data-step);
  display: block;
  margin: 0 auto;
  background: #DFE3E4;
  width: 45px;
  height: 45px;
  text-align: center;
  margin-bottom: 5px;
  line-height: 45px;
  border-radius: 100%;
  position: relative;
  z-index: 1; }

.steps > li:after {
  content: '';
  position: absolute;
  display: block;
  background: #DFE3E4;
  width: 100%;
  height: 0.5em;
  top: 1.25em;
  left: 50%;
  margin-left: 1.5em\9; }

.steps > li:last-child:after {
  display: none; }

.steps > li.is-complete {
  color: #21771b; }

.steps > li.is-complete:before,
.steps > li.is-complete:after {
  color: #21771b;
  background-color: #87ec6d;
  -webkit-box-shadow: inset 0 0 7px 0 rgba(0, 0, 0, 0.3);
  -moz-box-shadow: inset 0 0 7px 0 rgba(0, 0, 0, 0.3);
  box-shadow: inset 0 0 7px 0 rgba(0, 0, 0, 0.3); }

.steps > li.is-active {
  color: #185698; }

.steps > li.is-active:before {
  color: #185698;
  background-color: #84d0fd;
  border-color: #3694f7;
  -webkit-box-shadow: 4px 4px 20px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 4px 4px 20px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 4px 4px 20px 0px rgba(0, 0, 0, 0.2); }

/**
 * Needed for IE8
 */
.progress__last:after {
  display: none !important; }

@media (max-width: 800px) {
  .steps {
    padding: 16px 0; } }
